import gird from '@/components/common/gird'
export default {
	data() {
		return {
			// 菜单名称
			menus:[
				"首页",
				"系统管理",
				"综合管理",
				"用户管理",
				"员工管理",
				"人员追踪",
				"查询统计",
				"考勤管理",
				"系统帮助"
			],
			// 菜单的id值，用来切换菜单
			scrollIntoindex: "top0",
			// 菜单的索引
			topBarIndex: 0
		}
	},
	onLoad() {

	},
	methods: {
		changTap(index) {
		  if (this.topBarIndex === index) return false;
		  this.topBarIndex = index;
		  this.scrollIntoindex = `top${index}`;
		},
		// 切换视图
		onChangeTap(e) {
		  this.changTap(e.detail.current);
		}
	},
	components:{
		gird
	}
}
